import React from 'react';
import { connect } from 'react-redux';
import Humans from './Humans';
import PreLoader from '../PreLoader/PreLoader';
import { getUsersTC } from '../../Redux/users-Reduser';
import { followTC, unfollowTC } from '../../Redux/users-Reduser';
import { disableIdUpdate } from '../../Redux/users-Reduser';


class HumansAPI extends React.Component {
    
    componentDidMount() {
        this.props.getUsersTC(this.props.pageSize,this.props.pageTotal);

    }
    setNewPageTotal = (newPageTotal) => {

        this.props.getUsersTC(this.props.pageSize,newPageTotal);

    }
    render() {
        return (
            this.props.isLoaded ? <PreLoader/> : <Humans 
                users={this.props.users}
                setNewPageTotal={this.setNewPageTotal}
                pageCount={this.props.pageCount}
                pageSize={this.props.pageSize}
                unfollowTC={this.props.unfollowTC}
                followTC={this.props.followTC}
                disableId={this.props.disableId}
                disableIdUpdate={this.props.disableIdUpdate}

            />
        )
    }
}

let mapStateToProps = (state) => {
    return {
        users: state.humansPage.users,
        pageCount: state.humansPage.pageCount,
        pageTotal: state.humansPage.pageTotal,
        pageSize: state.humansPage.pageSize,
        isLoaded: state.humansPage.isLoaded,
        disableId: state.humansPage.disableId,
        
    }
}

let obj = {
    followTC,
    unfollowTC,
    disableIdUpdate,
    disableIdUpdate,
    getUsersTC
}

const HumansContainer = connect(mapStateToProps,obj )(HumansAPI);
export default HumansContainer;